.hex-board {
  /* width: 614px; */
  /* height: 662.22px; */
  height: 706.22px;
  /* margin: 47px auto; */
  /* margin: auto auto; */
  position: relative;
  overflow: hidden;
  width: 79vw;
  position: relative;
  overflow: hidden;
  /* background-color: #fff; */
}

.hexagon {
  position: absolute;
  /* background-color: #ffffff; */
  background-color: #434B57;
  border-radius: 2px;
  /* margin-left: 2px; */
  width: 21px; 
  height: 14px;
  /* width: 16px; 
  height: 11px; */
  box-sizing: border-box;
  box-shadow: 0px 0px 0px 1px #434b571d;
  transition: all 0.1s;
  border: 0.4vh solid transparent;
  transform: rotate(90deg);
}

.hexagon:before, .hexagon:after {
  content: "";
  border: inherit;
  position: absolute;
  top: -0.375vh;
  left: -0.4vh;
  background-color: #434B57;
  transition: all 0.1s;
  border-radius: inherit;
  height: 100%;
  width: 100%;
}

.hexagon:hover, .hexagon:hover:before, .hexagon:hover:after {
  /* background-color: #1B8BCD; */
  animation: hexagon-pulsate 0.2s 1 linear forwards;
  /* transform: scale(1.1); */
}

@keyframes hexagon-pulsate{
  0% {
    background-color: #434B57;
  }
  /* 2.5% {
    background-color: #1B8BCD;
  }
  50% {
    background-color: #434B57;
  }
  70% {
    background-color: #1B8BCD;
  } */
  100% {
    background-color: #1B8BCD;
  }
}

.hexagon:before {
  transform: rotate(60deg);
}
.hexagon:after {
  transform: rotate(-60deg);
}